$primary-color: #6da4c0;
$secondary-color: #467288;
$text-color: rgb(234, 240, 240);
$border-color: bisque;
$background-color: rgba($primary-color, 0.5);

.container {
  margin-top: 50px;
  padding: 10px;
  color: $text-color;
  background-color: $background-color;
}
.commentsWrapper {
  max-height: 300px; 
  overflow-y: auto; 
}
.commentsWrapper::-webkit-scrollbar {
  width: 8px; 
}
.commentsWrapper::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
.commentsWrapper::-webkit-scrollbar-thumb {
  background: #888; 
  border-radius: 6px; 
}
.commentsWrapper::-webkit-scrollbar-thumb:hover {
  background: #555;
}
.commentsWrapper.hasReviews {
  overflow-y: hidden; 
}
.commentBox {
  margin: 10px;
}
.authorBox {
  background-color: rgba($secondary-color, 0.6);
  padding: 8px;
  width: max-content;
  border-top-right-radius: 10px;
  border: 1px solid $border-color;
}
.contentBox {
  background-color: rgba($primary-color, 0.62);
  padding: 6px 10px;
  border-top-right-radius: 6px;
  border-bottom-right-radius: 6px;
  border: 1px solid $border-color;
  letter-spacing: 0.04em;
}
.noReviewSms {
  color: white;
  font-weight: bold;
}